import React, { Component } from "react";
import styled from "styled-components";

const Div = styled.section`
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  margin-top: 35px;
  display: flex;
  @media screen and (max-width: 767px) {
    flex-wrap: wrap;
  }
`;

const Text = styled.div`
  width: 80%;
  padding: 16px;
  order: 1;
  @media screen and (max-width: 767px) {
    width: 100%;
    order: 2;
  }
`;
const Image = styled.div`
  width: 20%;
  padding-bottom: 20%;
  background: url("/static/img/bendo-1.png") no-repeat center;
  background-size: cover;
  border-left: 1px solid #e0e0e0;
  order: 2;
  @media screen and (max-width: 767px) {
    width: 100%;
    padding-bottom: 50%;
    order: 1;
  }
`;

class Source extends Component {
  render() {
    return (
      <Div>
        <Text>
          <h3>
            Stage 1 in the Design Thinking Process: Empathise with Your Users
          </h3>
          <p className="source-content">
            Design Thinking cannot begin without a deeper understanding of the
            people you are designing for. In order to gain thoseâ€¦
          </p>
          <p>www.interaction-design.org</p>
        </Text>
        <Image />
      </Div>
    );
  }
}

export default Source;
